@import-normalize;
@import "./variaveis";

body {
    margin: 0;
    padding: 0;
    min-height: 100vh;
    background-image: linear-gradient($azul-escuro, $azul-claro);
    box-sizing: border-box;
  }

  body::-webkit-scrollbar {
    width: 5px;
  }
  body::-webkit-scrollbar-track {
    background: linear-gradient($azul-escuro, $azul-claro);
  }
  body::-webkit-scrollbar-thumb {
    background-color: $azul-claro;
    border-radius: 20px;
  }

  @font-face {
    font-family: $fonte-normal;
    src:local($fonte-normal), url("./fontes/GandhiSans-Regular.otf") format("truetype");
    font-weight: 500;
    font-display: swap;
  }
  @font-face {
    font-family: $fonte-bold;
    src:local( $fonte-bold), url("./fontes/GandhiSans-Bold.otf") format("truetype");
    font-weight: 700;
    font-display: swap;
  }
  